<template>
  <div role="techlist">
   <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block 
        @click="collapseOpen('tec')"
        class="client-color">
          <i class="fas fa-cogs"></i>
          Tecnologias
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="tec" accordion="tec" role="tabpanel">
        <b-card-body>
          <spa></spa>
          <pwa></pwa>
        </b-card-body>
      </b-collapse>
    </b-card>    
  </div>
</template>

<script>
import spa from './technologys/spa.vue';
import pwa from './technologys/pwa.vue';
export default {
  name: "tecnos", 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  components:{
    spa,
    pwa
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>