<template>
  <div role="dbslist">
  <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block 
        @click="collapseOpen('dbc')"
        class="client-color">
          <i class="fas fa-database"></i>
          Bases de Dados
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="dbc" accordion="dbc" role="tabpanel">
        <b-card-body>
          - IndexedDB
        </b-card-body>
      </b-collapse>
    </b-card>   
  </div>
</template>

<script>

export default {
  name: "Dbs", 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>