<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block 
        @click="collapseOpen('langsclient')"
        class="client-color">
          <i class="fas fa-code"></i>
          Linguagens
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
          </b-button>
      </b-card-header>
      <b-collapse id="langsclient" accordion="langsclient" role="tabpanel">
        <b-card-body>
          <Lang
            v-bind:langs="[{
                name: 'HTML',
                frameworks: [''],
                managers: [''],
                libs: [''],
                orms: [''],
                color: 'java-color',
                icon:'fa-html5',
                class: 'fas fa-info-circle'
            },

            {
                name: 'CSS',
                frameworks: ['Bootstrap', 'Materialize', 'Bulma'],
                libs: ['Sass', 'LESS', 'Stylus'],
                color: 'css-color',
                icon:'fa-css3-alt',
                class: 'fas fa-plus'
            },
            {
                name: 'JavaScript',
                frameworks: ['Angular', 'Vue.js', 'Ember.js', 'Aurelia' ],
                managers: ['NPM', 'Yarn', 'Bower'],
                libs: ['React', 'Polymer', 'JQuery'],
                color: 'js-color',
                icon:'fa-js',
                class: 'fas fa-plus'
            }
            ]"
          ></Lang>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import Lang from "./lang.vue";

export default {
  name: "Languages",
  components: {
    Lang
  }, 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>

