<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="server-color p-0" role="tab">
        <b-button block 
        class="server-color"
        @click="collapseOpen('langs')">
          <i class="fas fa-terminal"></i>
          Linguagens e Plataformas
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="langs" accordion="langs" role="tabpanel">
        <b-card-body>
          <Lang
            v-bind:langs="[{
                name: 'Node.js',
                frameworks: ['Express.js', 'Meteor.js', 'Sail.js', 'Koa.js' ],
                managers: ['NPM', 'Yarn'],
                libs: ['Jasmine', 'Jest', 'JWT', 'Passport'],
                cmsArr: ['Ghost', 'Keystone.js', 'strapi'],
                orms: ['Mongoose', 'Sequelize', 'Loopback', 'TypeORM'],
                color: 'node-color',
                icon: 'fab fa-node-js',
                class: 'fas fa-plus'
            },
            {
                name: 'Java',
                frameworks: ['Spring', 'JSF', 'Grails', 'Struts'],
                managers: ['Maven', 'Gradle'],
                libs: ['Spring Security'],
                cmsArr: ['BloomReach', 'Crafter CMS', 'Built.io', 'OpenCMS', 'nCMS'],
                orms: ['Hibernate', 'JPA', 'Spring Data'],
                color: 'java-color',
                icon: 'fab fa-java',
                class: 'fas fa-plus'
            },
            {
                name: 'PHP',
                frameworks: ['Laravel', 'Zend Framework', 'CodeIgniter', 'CakePHP'],
                managers: ['Composer'],
                libs: ['Guzzle', 'Opauth'],
                cmsArr: ['WordPress', 'Joomla', 'Drupal', 'Magento'],
                orms: ['Doctrine ORM', 'Eloquent ORM', 'Torm'],
                color: 'php-color',
                icon: 'fab fa-php',
                class: 'fas fa-plus'
            },
            {
                name: 'Python',
                frameworks: ['Django', 'TurboGears', 'web2py'],
                managers: ['pipenv'],
                libs: ['-'],
                cmsArr: ['Django CMS', 'Mezzanine', 'Plone'],
                orms: ['Django ORM', 'Peewee', 'PonyORM'],
                color: 'python-color',
                icon: 'fab fa-python',
                class: 'fas fa-plus'
            },
            {
                name: 'C#',
                frameworks: ['.Net'],
                managers: ['NuGet'],
                libs: ['-'],
                cmsArr: ['DotNetNuke', 'mojoPortal', 'Umbraco', 'Orchard'],
                orms: ['Entity Framework', 'OrmLite'],
                color: 'csharp-color',
                icon: 'fas fa-angle-double-right',
                class: 'fas fa-plus'
            }
            ]"
          ></Lang>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import Lang from "./lang.vue";

export default {
  name: "Languages",
  components: {
    Lang
  }, 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>

