<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block  @click="collapseOpen('client')" class="client-color">
          <i class="fas fa-desktop"></i>
          Cliente (Front-end)
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="client" accordion="client" role="tabpanel">
        <b-card-body>

          <clientmenu></clientmenu>

        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import clientmenu from "./clientmenu.vue";

export default {
  name: "Client",
  components: {
    clientmenu
  },
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
   methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>