          <strong>Mapeamento objeto-relacional</strong> ou
          <strong>ORM</strong>, do ingls
          <strong>
            <em>
              Object-relational
              mapping
            </em>
          </strong>  mais um recurso que d ao desenvolvedor mais produtividade e menos preocupao
