<template>
  <div role="techlist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block  @click="collapseOpen('tecs')" variant="primary">
          <i class="fas fa-cogs"></i>
          Tecnologias
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="tecs" accordion="tecs" role="tabpanel">
        <b-card-body>
          <Api></Api>
          <ProtocolHttp></ProtocolHttp>        
          <Frameworks></Frameworks>          
          <depmanager></depmanager>
          <Cms></Cms>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import Api from './technologys/api.vue';
import depmanager from './technologys/devmanager.vue';
import Frameworks from './technologys/framework.vue';
import ProtocolHttp from './technologys/http.vue';
import Cms from './technologys/cms.vue';

export default {
  name: "tecnos",
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
   methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  },
  components:{
    Api,
    depmanager,
    Frameworks,
    ProtocolHttp,
    Cms
  }
};
</script>