<template>
  <div role="tablist">
    <b-card no-body class="mt-2" v-for="(lang, index) in langs" :key="index">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block  @click="collapseOpen(lang, index)" v-bind:class="lang.color">
          <i v-bind:class="lang.icon"></i>
          {{ lang.name }}
          
          <span class="float-right">
            <i v-bind:class="signal[index].class"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse v-bind:id="lang.name" v-bind:accordion="lang.name" role="tabpanel">
        <b-card-body>
          <Properties
            v-bind:name="lang.name"
            v-bind:frameworks="lang.frameworks"
            v-bind:managers="lang.managers"
            v-bind:libs="lang.libs"
            v-bind:cmsArr="lang.cmsArr"
            v-bind:orms="lang.orms"
            v-bind:color="lang.color"
          ></Properties>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import Properties from "./properties.vue";

export default {
  name: "Lang",
  props: {
    langs: Array
  },
  components: {
    Properties
  }, 
  data() {    
    return {
      signal: this.$props.langs.map((item)=>{
       return {class : item.class}
        })    
    }
  },
  methods: {    
    collapseOpen(id, index) {         
      this.signal[index].class = (this.signal[index].class === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id.name);
    }
  }
};
</script>

