<template>
  <div role="tablist" >
    <b-form-input    
      id="ipEmail"
      ref="focusThis"
      :state="showVlEmail"
      v-model="email"
      class="ipNewsletter"
      aria-describedby="input-live-help input-live-feedback"
      placeholder="Digite aqui o seu e-mail"
      @keyup.enter="sendEmail()"
    ></b-form-input>
    <b-form-invalid-feedback class="mb-2" d="input-live-feedback" v-html="msgFeed"></b-form-invalid-feedback>

    <b-button 
      block 
      class="text-center" 
      @click="sendEmail()" 
      variant="success">
      Quero Receber Mais Contedo
      <b-spinner 
      v-if="showSpinner" 
      small 
      type="grow" 
      ></b-spinner>
    </b-button>
  </div>
</template>

<script>

export default {
  name: "newsletter",
  data() {
    return {
      email: "",
      msgFeed: "",
      showVlEmail: null,
      showSpinner: false
    };
  },
  methods: {
         
    sendEmail() {
      this.showSpinner = true;
      if (this.email === "") {
        this.msgFeed =
          "<strong>Digite seu e-mail no campo acima</strong>, para receber mais contedos!";
        this.showVlEmail = false;
         this.showSpinner = false;
      } else if (this.validaEmail(this.email)) {
        this.showVlEmail = true;       
        fetch("https://rafaeldeveloper.online/svnewsletter/svemail", {
          method: "post",
          headers: {
            "Content-type": "application/x-www-form-urlencoded; charset=UTF-8"
          },
          body: `email=${this.email}`
        })
          .then(data => data.json())
          .then(result => {
             this.showSpinner = false;
            if (result.status) {
              this.$emit('changeState')
              localStorage.setItem("email", "sended");
            }
          }).catch(err=>{
            this.showSpinner = false;
            alert('No foi possvel enviar no momento, tente novamente mais tarde! '+err)
          });
      } else {
        this.showVlEmail = false;
        this.showSpinner = false;
        this.msgFeed =
          "Digite um e-mail <strong>VLIDO</strong>, para receber mais contedos!";
      }
    },
    validaEmail: function(email) {
      var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return re.test(email);
    }
  } 
};

</script>