<template>
  <div role="techlist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block 
        @click="collapseOpen('tecsServ')"
        class="server-color">
          <i class="fas fa-cogs"></i>
          Tecnologias
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="tecsServ" accordion="tecs" role="tabpanel">
        <b-card-body> 
          <Orm></Orm>          
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import Orm from './technologys/orm.vue';

export default {
  name: "tecnos",
  components:{
    Orm
  }, 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>