<template>
  <div role="dbslist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block @click="collapseOpen('relac')" variant="dark">
          <i class="fas fa-table"></i>
          Relacionais
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="relac" accordion="relac" role="tabpanel">
        <b-card-body>
          <ul class="prop">
            <li>MySQL</li>
            <li>SqlServer</li>
            <li>Oracle</li>
            <li>PostgreSQL</li>
            <li>SQLite</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "Relacional",
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {
    collapseOpen(id) {
      this.signal =
        this.signal === "fas fa-plus" ? "fas fa-minus" : "fas fa-plus";
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>