<template>
  <div class="mt-2">
    <b-button block @click="showModal" variant="info">
      <i class="far fa-file"></i>
      SPAs
      <span class="float-right">
        <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <b-modal ref="modalspa" hide-footer title="SPA ou Single Page Aplication">
      <div class="d-block">
        <p>
          <strong>Single Page Aplication</strong> ou em portugus
          <strong>Aplicao de uma pgina s</strong>  uma
          poderosa e moderna tecnologia, que, como o nome j diz, possibilita desenvolvermos toda a nossa aplicao web,
          em apenas uma pgina .
        </p>
        <p>Como assim? E quando eu precisar mostrar outras telas ou baixar contedo do servidor?</p>
        <p>
          Essa tecnologia utiliza diversos recursos do JavaScript e dos navegadores para poder modificar os componentes da
          pgina e fazer solicitaes ao servidor atravs de requisies HTTP. Assim, possibilitando a apresentao de
          outras telas e dados vindo do servidor sem a necessidade de recarregar a pgina ou abrir uma nova pgina. O
          JavaScript se encarrega de atualizar apenas os elementos necessrios para mostrar uma nova tela.
        </p>
        <p>
          <strong>E qual a vantagem disso?</strong>
        </p>
        <p>
          Alm da excelente
          <strong>experincia do usurio</strong>, essa prtica gera uma notvel diminuio do consumo de
          dados vindo do servidor, o que  excelente para
          <strong>economia dos dados mveis dos usurios</strong> e para a
          <strong>economia dos recursos dos servidores</strong>, que no precisam mais ficar processando pginas inteiras
          e enviando para o cliente.
        </p>
        <p>
          Por essas e outras grandes vantagens as SPAs vem adquirindo um grande nmero de adeptos entre os desenvolvedores
          e empresas de desenvolvimento web.
        </p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "spa",
  methods: {
    showModal() {
      this.$refs.modalspa.show();
    },
    hideModal() {
      this.$refs.modalspa.hide();
    }
  }
};
</script>