<template>
  <div role="tablist">
    <b-card no-body class="mt-2" v-for="(lang, index) in langs" :key="index">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block @click="collapseOpen(lang.name, index)" v-bind:class="lang.color">
          <i v-bind:class="'fab '+lang.icon"></i>
          {{ lang.name }}
          <span class="float-right">
            <i v-bind:class="signal[index].class"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse v-bind:id="lang.name" v-bind:accordion="lang.name" role="tabpanel">
        <b-card-body>
          <Properties
            v-bind:name="lang.name"
            v-bind:frameworks="lang.frameworks"
            v-bind:managers="lang.managers"
            v-bind:libs="lang.libs"
            v-bind:orms="lang.orms"
            v-bind:color="lang.color"
          ></Properties>
        </b-card-body>
      </b-collapse>
    </b-card>
    <b-modal ref="modalHtml" hide-footer title="HTML">
      <div class="d-block">
        <p>
          O <strong>HTML</strong> (HyperText Markup Language,
          ou Linguagem de Marcao de HiperTexto)  a linguagem
          mais bsica e fundamental da web, no existe web sem
          HTML. O HTML  para a web como o chassi  para o carro, 
          como o alicerce e as colunas so para a casa e como as asas
          so para o avio. <br>
          Ele  o responsvel pela a estrutura de qualquer pgina
          e aplicao web.
        </p>
        <p>
          - Ah! mas eu uso o framework XYZ e com ele eu no escrevo
          nenhuma linha de HTML. Ok, tudo bem, voc no escreve,
          mas o framework gera o HTML, para voc por debaixo dos
          panos.
        </p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
import Properties from "./properties.vue";

export default {
  name: "Lang",
  props: {
    langs: Array
  },
  components: {
    Properties
  },
  data() {
    return {
      signal: this.$props.langs.map(item => {
        return {
          class: item.class
        };
      })
    };
  },
  methods: {
    collapseOpen(id, index) {
      if (id === "HTML") {
        this.$refs.modalHtml.show();
      } else {
        this.signal[index].class =
          this.signal[index].class === "fas fa-plus"
            ? "fas fa-minus"
            : "fas fa-plus";
        this.$root.$emit("bv::toggle::collapse", id);
      }
    },
    hideModal() {
      this.$refs.modalHtml.hide();
    }
  }
};
</script>

