<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button
          class="text-left btn"
          size="lg"
          block
          @click="collapseOpen('newsletter')"
          variant="primary"
        >
          <i class="fas fa-plus-square"></i>
          Mais Contedo
          <span class="float-right">
            <i v-bind:class="(signal)?'fas fa-minus':'fas fa-plus'"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="newsletter" v-model="signal">
        <b-card-body>
          <div v-if="invite === 'no-sended' || invite === null">
            
            <p>
              <strong>Se voc  como eu,</strong> que gosta de aprender
              <strong>
                de forma rpida, objetiva
                e sem enrolao.
              </strong>
            </p>
            <p>
              <strong>Deixei seu e-mail</strong>, que eu te enviarei contedos
              <strong>prticos, eficientes e gratuitos.</strong>
            </p>  
            <sendemail v-on:changeState="invite ='sended'" ></sendemail>
          </div>
          <div v-if="invite === 'sended' || invite != null">
            <b-alert variant="success" show>
              J recebemos o seu endereo de e-mail, agora  s ficar de
              olho
              <i
                class="fas fa-eye"
              ></i> no seu e-mail, que em breve estaremos te enviando mais
              contedos, dicas, informaes e promoes.
              <i
                class="fas fa-thumbs-up"
              ></i>
            </b-alert>
          </div>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import sendemail from './sendemail.vue';
export default {
  name: "newsletter",
  data() {
    return {
      showCollapse: this.signal,
      invite: localStorage.getItem('email')
    };
  },  
  props: {
    signal: Boolean
  },
  components: {
    sendemail
  },
  methods: {
    collapseOpen(id) {
     this.$emit('changeNewsletter', id)
    },    
  } 
};

</script>
