<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button
          block
          v-on:click="collapseOpen('framework'+ name, 0)"
          v-bind:class="color"
        >
        <i class="fas fa-shapes"></i>
        Frameworks
        <span class="float-right">
          <i v-bind:class="signal[0].class"></i>
        </span>
        </b-button>
      </b-card-header>
      <b-collapse
        v-bind:id="'framework'+ name"
        v-bind:accordion="'framework'+ name"
        role="tabpanel"
      >
        <b-card-body>
          <ul>
            <li v-for="(framework, index) in frameworks" :key="index">{{ framework }}</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mt-2" v-if="name != 'CSS'">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button
          block
          v-on:click="collapseOpen('manager'+ name, 1)"
          v-bind:class="color"
        >
        <i class="fas fa-tasks"></i>
        Gerenciadores de Dependncias
        <span class="float-right">
          <i v-bind:class="signal[1].class"></i>
        </span>
        </b-button>
      </b-card-header>
      <b-collapse v-bind:id="'manager'+ name" v-bind:accordion="'manager'+ name" role="tabpanel">
        <b-card-body>
          <ul>
            <li v-for="(manager, index) in managers" :key="index">{{ manager}}</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>

    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button
          block
          v-on:click="collapseOpen('lib'+ name, 2)"
          v-bind:class="color"
        >
        <i class="fas fa-toolbox"></i>
        Ferramentas e bibliotecas
        <span class="float-right">
          <i v-bind:class="signal[2].class"></i>
        </span>
        </b-button>
      </b-card-header>
      <b-collapse v-bind:id="'lib'+ name" v-bind:accordion="'lib'+ name" role="tabpanel">
        <b-card-body>
          <ul>
            <li v-for="(lib, index) in libs" :key="index">{{ lib }}</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>
    
  </div>
</template>

<script>
export default {
  name: "Properties",
  props: {
    name: String,
    frameworks: Array,
    managers: Array,
    libs: Array,
    orms: Array,
    color: String
  }, 
  data() {
    return {
      signal:[
        { class: 'fas fa-plus'},
        { class: 'fas fa-plus'},
        { class: 'fas fa-plus'},
        { class: 'fas fa-plus'},
        ] 
    };
  },
  methods: {    
    collapseOpen(id, index) { 
      this.signal[index].class = (this.signal[index].class === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>