<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button 
        class="text-left btn" 
        size="lg" 
        block         
        @click="collapseOpen('web')"
        variant="primary">
          <i class="fas fa-globe"></i>
          WEB (FullStack)
          <span class="float-right">
            <i v-bind:class="(signal)?'fas fa-minus':'fas fa-plus'"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="web" v-model="signal">
        <b-card-body>         
          <techs></techs>
          <servidor></servidor>
          <cliente></cliente>
          
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import techs from "./techs/tecnos.vue";
import servidor from "./serv/servidor.vue";
import cliente from "./client/cliente.vue";

export default {
  data() {
    return {
      showCollapse: this.signal
    };
  },
  props: {
    signal: Boolean
  },
  components: {
    servidor,
    cliente,
    techs
  },
  methods: {    
    collapseOpen(id) { 
      this.$emit('changeWeb', id)
      localStorage.setItem('access', 1)
    }
  }
};
</script>