<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block @click="collapseOpen('serv')" class="server-color">
          <i class="fas fa-server"></i>
          Servidor (Back-end)
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
          </b-button>
      </b-card-header>
      <b-collapse id="serv" accordion="serv" role="tabpanel">
        <b-card-body>

          <servmenu></servmenu>

        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
import servmenu from "./servmenu.vue";

export default {
  name: "servidor",
  components: {
    servmenu
  },
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
   methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>