<template>
  <div role="tablist">
    <framework v-bind:name="name" 
    v-bind:frameworks="frameworks"
    v-bind:color="color"></framework>
    <manager v-bind:name="name" 
    v-bind:managers="managers"
    v-bind:color="color"></manager>
    <libs v-bind:name="name" 
    v-bind:libs="libs"
    v-bind:color="color"></libs>
    <cms v-bind:name="name" 
    v-bind:cmsArr="cmsArr"
    v-bind:color="color"></cms>
    <orm v-bind:name="name" 
    v-bind:orms="orms"
    v-bind:color="color"></orm>    
  </div>
</template>

<script>
import framework from './properties/framework.vue';
import manager from './properties/depmang.vue';
import cms from './properties/cms.vue';
import orm from './properties/orm.vue';
import libs from './properties/libs.vue'

export default {
  name: "Properties",
  props: {
    name: String,
    frameworks: Array,
    managers: Array,
    libs: Array,
    cmsArr: Array,
    orms: Array,
    color: String
  },
  components:{
    framework,
    manager,
    cms,
    orm,
    libs
  },
   data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {
    collapseOpen: function(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>