<template>
  <div class="mt-2">
    <b-button block @click="showModal" variant="primary">
      <i class="fas fa-plug"></i>
      HTTP
      <span class="float-right">
       <i class="fas fa-info-circle"></i>
      </span>
    </b-button>

    <!-- Modal Component -->
    <b-modal ref="modalHttp" hide-footer title="HTTP Protocol">
      <div class="d-block">
        <p> <strong>HTTP</strong>  um protocolo que permite a obteno de recursos, tais como documentos HTML.  a base de
        qualquer troca de dados na Web e um protocolo cliente-servidor, o que significa que as requisies so iniciados
        pelo destinatrio, geralmente um navegador da Web. Um documento completo  reconstrudo a partir dos diferentes
        sub-documentos obtidos, como por exemplo texto, descrio do layout, imagens, vdeos, scripts e muito mais.</p>
    <p>Clientes e servidores se comunicam trocando mensagens individuais (em oposio a um fluxo de dados). As mensagens
        enviadas pelo cliente, geralmente um navegador da Web, so chamadas de <strong>solicitaes</strong>, ou tambm
        <strong>requisies</strong>, e as mensagens enviadas pelo servidor como resposta so chamadas de
        <strong>resposta</strong>.</p>
    <p>Projetado no incio da dcada de 1990, o HTTP  um protocolo extensvel que evoluiu ao longo do tempo.  um
        protocolo de camada de aplicao que  enviado sobre TCP, ou em uma conexo TCP criptografada com TLS embora
        qualquer protocolo de transporte confivel possa, teoricamente, ser usado. Devido  sua extensibilidade, ele 
        usado para no apenas buscar documentos de hipertexto, mas tambm imagens e vdeos ou publicar contedo em
        servidores, como nos resultados de formulrio HTML . O HTTP tambm pode ser usado para buscar partes de
        documentos para atualizar pginas da Web sob demanda.</p>
    <p>Trecho retirado do site <strong>MDN web docs</strong>. <a
            href='https://developer.mozilla.org/pt-BR/docs/Web/HTTP/Overview' target='_blank'
            class='url'>https://developer.mozilla.org/pt-BR/docs/Web/HTTP/Overview</a></p>
    <p>&nbsp;</p>
      </div>
      <b-button class="mt-3 float-right" variant="secondary" @click="hideModal">Fechar</b-button>
    </b-modal>
  </div>
</template>

<script>
export default {
  name: "Http",
  methods: {
    showModal() {
      this.$refs.modalHttp.show();
    },
    hideModal() {
      this.$refs.modalHttp.hide();
    }
  }
};
</script>