<template>
  <div role="tablist">
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button
          block
          v-on:click="collapseOpen('manager'+ name)"
          v-bind:class="color"
        >
        <i class="fas fa-tasks"></i>
        Gerenciadores de Dependncias
         <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse v-bind:id="'manager'+ name" v-bind:accordion="'manager'+ name" role="tabpanel">
        <b-card-body>
          <ul class="prop">
            <li v-for="(manager, index) in managers" :key="index">{{ manager}}</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>    
  </div>
</template>

<script>
export default {
  name: "depmang",
  props: {
    name: String,    
    managers: Array,
    color: String
  },
   data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {
    collapseOpen: function(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>