<template>
  <div id="app" >
    <b-navbar type="dark" variant="primary">
      <b-navbar-brand href="#">Web Dev Overview</b-navbar-brand>
    </b-navbar>
    <b-container>
      <b-row align-h="center">
        <b-col  md="8">
          <Intro  v-bind:signal="signalIntro" v-on:changeIntro="collapseOpen($event)"></Intro>
        </b-col>
      </b-row>
      <b-row align-h="center">
        <b-col  md="8">
          <Web v-bind:signal="signalWeb" v-on:changeWeb="collapseOpen($event)"></Web>
        </b-col>
      </b-row>
      <b-row align-h="center">
        <b-col  md="8">
          <newsletter v-bind:signal="signalNewsletter" v-on:changeNewsletter="collapseOpen($event)"></newsletter>
        </b-col>
      </b-row>
    </b-container>
    
    <b-modal  ref="my-modal" 
      centered       
      v-model="modalShow"
      hide-header
      hide-header-close
      no-close-on-esc
      hide-footer
      no-close-on-backdrop
      >
      <p><strong>Cadastre seu e-mail</strong>, 
      para continuar acessando e receber mais contedos como esse.
      </p>
     <sendemail v-on:changeState="modalShow=false"></sendemail>     
    </b-modal>
  </div>
</template>

<script>

import Intro from "./components/introduction/introduction.vue";
import Web from "./components/web.vue";
import Newsletter from "./components/newsletter/newsletter.vue";
import sendemail from "./components/newsletter/sendemail.vue";

export default {
  name: "app",
  components: {
    Intro,
    Web,
    Newsletter,
    sendemail
  },
   data() {
      return {
        modalShow: false /* (localStorage.getItem('access') === null || localStorage.getItem("email") === 'sended')? false : true */,
        invite: localStorage.getItem("email"),
        email: "",
        msgFeed: "",
        showVlEmail: null,
        signalIntro: false,
        signalWeb: false,
        signalNewsletter: false

      }
    },
  methods: {
    
    collapseOpen(id) { 
      if(id === 'intro'){
        this.signalIntro = !this.signalIntro;
        this.signalWeb = false;
        this.signalNewsletter = false; 
      }
      if(id === 'web'){
        this.signalWeb = !this.signalWeb;
        this.signalIntro = false;
        this.signalNewsletter = false; 
      }
      if(id === 'newsletter'){
        this.signalNewsletter = !this.signalNewsletter;
        this.signalWeb = false;
        this.signalIntro = false; 
      }      
     
    },   
    
    showModal() {
      this.$refs["my-modal"].show();
    },
    hideModal() {
      this.$refs["my-modal"].hide();
    }
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
