<template>
  <div role="dbslist">
  <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block 
         @click="collapseOpen('norelac')"
         variant="dark">
          <i class="fas fa-stream"></i>
          Non-relacionais
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
        </b-button>
      </b-card-header>
      <b-collapse id="norelac" accordion="norelac" role="tabpanel">
        <b-card-body>
          <ul class="prop">            
            <li>Mongodb</li>
            <li>Cassandra</li>
            <li>Voldemort</li>
            <li>CouchDB</li>
            <li>Riak</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>   
  </div>
</template>

<script>

export default {
  name: "Nonrelacional", 
  data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {    
    collapseOpen(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>