<template>
  <div role="tablist">   
    <b-card no-body class="mt-2">
      <b-card-header header-tag="header" class="p-0" role="tab">
        <b-button block v-on:click="collapseOpen('Orms'+ name)"
         v-bind:class="color">
         <i class="fas fa-map"></i>
         Orms
          <span class="float-right">
            <i v-bind:class="signal"></i>
          </span>
         </b-button>
      </b-card-header>
      <b-collapse v-bind:id="'Orms'+ name" v-bind:accordion="'Orms'+ name" role="tabpanel">
        <b-card-body>
          <ul  class="prop">
            <li 
              v-for="(orm, index) in orms" :key="index">{{ orm }}</li>
          </ul>
        </b-card-body>
      </b-collapse>
    </b-card>
  </div>
</template>

<script>
export default {
  name: "orm",
  props: {
    name: String,
    orms: Array,
    color: String
  },
   data() {
    return {
      signal: `fas fa-plus`
    };
  },
  methods: {
    collapseOpen: function(id) { 
      this.signal = (this.signal === 'fas fa-plus')? 
      'fas fa-minus':'fas fa-plus';
      this.$root.$emit("bv::toggle::collapse", id);
    }
  }
};
</script>